<script>
import {reactive} from 'vue'
import {mapState, mapGetters} from 'vuex'
import Block from '@/components/Block1.vue'
import MallItem from '@/components/MallItem.vue'

export default {
  name: 'Home',
  components: {Block, MallItem},

  setup() {
    const state = reactive({
      isTop: true,
      keyword: "",
      swipe: [
        {image: require("@/assets/banner1.png"), link: ""},
        {image: require("@/assets/banner2.png"), link: ""},
      ],
      grid: [
        {icon: require("@/assets/icon1.png"), name: "鲜花直送", link: "/mall/list?cid=2"},
        {icon: require("@/assets/icon2.png"), name: "幸福西饼", link: "/mall/list?cid=11"},
        {icon: require("@/assets/icon3.png"), name: "礼品定制", link: "/mall/list?cid=4"},
        {icon: require("@/assets/icon5.png"), name: "充值中心", link: "/recharge"},
        {icon: require("@/assets/icon4.png"), name: "积分转让", link: "/my/exchange"},
        {icon: require("@/assets/icon6.png"), name: "樊登读书", link: "/mall/list?cid=107"},
        {icon: require("@/assets/icon7.png"), name: "视频会员", link: "/mall/list?cid=20"},
        {icon: require("@/assets/icon8.png"), name: "电影票", link: "/mall/list?cid=37"},
        {icon: require("@/assets/icon10.png"), name: "打车券", link: "/mall/list?cid=108"},
        {icon: require("@/assets/icon9.png"), name: "购物卡", link: "/mall/list?cid=106"},
      ],
      list: []
    });
    return {state};
  },

  created() {
    let arr = this.config.banner.split(",");
    if (arr.length > 0){
      this.state.swipe = [];
      for (let i in arr) {
        this.state.swipe.push({image: arr[i]});
      } 
    }
    
    this.$http.get("/mall/itemlist?recommend=1&limit=10").then((data) => {
      this.state.list = this.state.list.concat(data);
    });
  },

  computed: {
    bg() {
      return this.state.isTop ? "transparent" : "#FFF";
    },
    logo() {
      return this.config.logo.length > 10 ? this.config.logo : "goods-collect";
    },
    ...mapState(["config", "profile"]),
    ...mapGetters(["authorized"]),
  },

  methods: {
    onSearch() {
      this.$router.push("/mall/list?keyword=" + this.state.keyword);
    }
  }

}
</script>
